
import Vue from "vue";
import Component from "vue-class-component";

@Component({
  props: {
    maxVisibleButtons: {
      type: Number,
      required: false,
      default: 3,
    },
    totalPages: {
      type: Number,
      required: true,
    },
    currentPage: {
      type: Number,
      required: true,
    },
  },
})
export default class PaginationPage extends Vue {
  private maxVisibleButtons!: number;
  private totalPages!: number;
  private currentPage!: number;
  public get startPage() {
    // When on the first page
    if (this.currentPage === 1) {
      return 1;
    }
    // When on the last page
    if (this.currentPage === this.totalPages) {
      return this.totalPages;
    }
    // When inbetween
    return this.currentPage - 1;
  }
  public get pages() {
    const range = [];
    for (
      let i = this.startPage;
      i <=
      Math.min(this.startPage + this.maxVisibleButtons - 1, this.totalPages);
      i++
    ) {
      range.push({
        name: i,
        isDisabled: i === this.currentPage,
      });
    }
    return range;
  }
  public get isInFirstPage() {
    return this.currentPage === 1;
  }
  public get isInLastPage() {
    return this.currentPage === this.totalPages;
  }
  public onClickFirstPage() {
    this.$emit("pagechanged", 1);
    window.scrollTo(0, 0);
  }
  public onClickPreviousPage() {
    this.$emit("pagechanged", this.currentPage - 1);
    window.scrollTo(0, 0);
  }
  public onClickPage(page: number) {
    this.$emit("pagechanged", page);
    window.scrollTo(0, 0);
  }
  public onClickNextPage() {
    this.$emit("pagechanged", this.currentPage + 1);
    window.scrollTo(0, 0);
  }
  public onClickLastPage() {
    this.$emit("pagechanged", this.totalPages);
    window.scrollTo(0, 0);
  }
  public isPageActive(page: number) {
    return this.currentPage === page;
  }
}
